// import logo from './logo.svg';
import './App.css';
import Navbar from './components/Navbar';
import TextForms from './components/TextForms';
import About from './components/About';
import Alert from './components/Alert';
import React from "react";
import { useState } from 'react';

import {
  BrowserRouter as Router,
  Route,
  // Link,
  Routes
} from "react-router-dom";

function App() {
  const [mode,setMode] = useState('light');//whether dark mode is ewnabled or not
  const [alert,setAlert]=useState(null);
  
  const showAlert=(message,type)=>{
      setAlert({
        msg: message,
        type: type,
      })

      setTimeout(() => {
        setAlert(null);
      }, 2000);
  }

  const removebodyclasses = ()=>{
    document.body.classList.remove('bg-light')
    document.body.classList.remove('bg-dark')
    document.body.classList.remove('bg-success')
    document.body.classList.remove('bg-primary')
    document.body.classList.remove('bg-danger')
    document.body.classList.remove('bg-warning')
  }
  const togglemode =(cls)=>{
    removebodyclasses();
    // document.body.classList.add('bg-'+cls);
    /*if(mode==='dark'){
      setMode('light');
      document.body.style.backgroundColor='white';
      showAlert("light mode has been enabled","primary");
    }
    else if(mode==='light'){
      setMode('dark');
      document.body.style.backgroundColor='#2d3063';
      showAlert("dark mode has been enabled","success");

    }*/

    if(cls==='dark'){
      document.body.style.backgroundColor='#1c1c1c';
      setMode('dark');
      showAlert("dark mode has been enabled","dark");
    }
    else if(cls==='light'){
      document.body.style.backgroundColor='#ffffff';
      showAlert("light mode has been enabled","light");
      setMode('light');
    }
    else if(cls==='success'){
      document.body.style.backgroundColor='#03571d';
      showAlert("green mode has been enabled","success");
      setMode('success');
    }
    else if(cls==='danger'){
      document.body.style.backgroundColor='#8a0005';
      showAlert("red mode has been enabled","danger");
      setMode('danger');
    }
    else if(cls==='warning'){
      document.body.style.backgroundColor='#dbd402';
      showAlert("yellow mode has been enabled","warning");
      setMode('warning');
    }
    else if(cls==='primary'){
      document.body.style.backgroundColor='#17077a';
      showAlert("blue mode has been enabled","primary");
      setMode('primary');
    }
  }
  return (
    <>
    <Router>
    {/* <Navbar title="Textutils" hometext="Home"/> */}
    <Navbar  title="Text Analyzer" mode={mode} togglemode={togglemode}/>
    <Alert alert={alert}/>
    <div className="container my-3">
    <Routes>
          <Route exact path="/about" element={<About />}>
            {/* <About /> */}
          </Route>
          <Route exact path="/"element={<TextForms showAlert={showAlert} heading="Enter The Text To Analyze" mode={mode} />}>
            {/* <TextForms showAlert={showAlert} heading="Enter The Text To Analyze" mode={mode} /> */}
          </Route>
    </Routes>
    {/*  <About/> */}
    {/* <TextForms showAlert={showAlert} heading="Enter The Text To Analyze" mode={mode} /> */}
    </div>
    </Router>
    </>
  );
}

export default App;
